import React, { useEffect, useRef } from 'react'
import './index.scss'

import Video from '../../mountain.mp4'
import Navigation, { NavigationLink } from '../../components/Navigation'
import About from '../../components/About'

const Main = () => {

    const mainRef = useRef<HTMLDivElement | null>(null);
    const aboutRef = useRef<HTMLDivElement | null>(null);

    const handleNavSelection = (link: NavigationLink) => {
        switch(link) {
            case 'main':
                if(!mainRef) return;

                mainRef.current?.scrollIntoView({
                    behavior: 'smooth'
                });
            break;
            case 'about':
                if(!aboutRef) return;

                aboutRef.current?.scrollIntoView({
                    behavior: 'smooth'
                });
            break;
        }
    }

    return (
        <main className="main">
            <section className="main__landing" ref={mainRef}>
                <video src={Video} autoPlay={true} loop={true} className="main__landing__video"></video>
                <div className="main__landing__content">
                    <div className="main__landing__person">
                        <h1 className="main__landing__hello">
                            Hello, I'm
                        </h1>
                        <h2 className="main__landing__fullname">
                            Joseph Harris
                        </h2>
                    </div>
                    <Navigation onSelect={v => handleNavSelection(v)} />
                </div>
            </section>
            <About reference={aboutRef} />
        </main>
    )
}

export default Main
